.nav {
  color: $black;
  text-transform: uppercase;
  li {
    display: inline-block;
    margin-bottom: 5px;
    @include sm {
      display: block; } }
  a {
    @include nav-link; }
  &--content {
    font-weight: 600;
    li {
      display: block; } }
  &--vert {
    font-weight: 600;
    text-align: center;
    li {
      display: block; } } }
